body {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
}

.card__image {
  width: 160px;
  height: 134px;
  padding-block: 32px 40px;
  padding-inline: 19px;
  display: block;
}

.card__title {
  padding: 0 16px;
  color: #060b35;
  margin: 0;
  padding-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin: 0;
  padding-bottom: 16px;
  padding-left: 16px;
  font-size: 10px;
  font-weight: 400;
}

.card__stars {
  justify-content: space-between;
  align-items: end;
  padding-bottom: 24px;
  display: flex;
}

.card__stars .card__starsList {
  margin: 0 0 0 16px;
  padding-left: 0;
  list-style: none;
  display: flex;
}

.card__stars .card__starsList .card__starsItem {
  box-sizing: border-box;
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__stars .card__starsList .card__starsItem:last-child {
  margin-right: 0;
}

.card__stars .card__starsList .card__starsItem:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
  background-repeat: no-repeat;
}

.card__stars .card__reviews {
  box-sizing: border-box;
  color: #060b35;
  margin: 0 16px 0 17px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  color: #616070;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding-left: 16px;
  font-size: 12px;
  font-weight: 400;
  display: flex;
}

.card__price span {
  color: #060b35;
  padding-left: 84px;
  padding-right: 16px;
  font-size: 16px;
  font-weight: 700;
}

.card__linkBtn {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  margin-block: 16px;
  margin-inline: 16px;
  padding: 12px 70px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  transition: all 1s;
  display: block;
}

.card__linkBtn:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

